<template>
	<div class="InventoryList">
		<header><i class="iconfont icon-xiangzuo1" @click="sendParams"></i><span>库存列表</span></header>
		<div class="container">
			<div class="title">
				<span class="span_1"  @click="SortArr('dataList','sku','SortCondition1',$event)"><span>商品代码</span><i class="iconfont"></i></span>
				<span class="span_2"  @click="SortArr('dataList','qty','SortCondition2',$event)"><span>数量</span><i class="iconfont"></i></span>
				<span class="span_3"  @click="SortArr('dataList','loc','SortCondition3',$event)"><span>库位</span><i class="iconfont"></i></span>
				<span class="span_4"  @click="SortArr('dataList','skuclass','SortCondition5',$event)"><span>分类</span><i class="iconfont"></i></span>
				<span class="span_5"  @click="SortArr('dataList','barcode','SortCondition4',$event)"><span>条形码</span><i class="iconfont"></i></span>
				
			</div>
			<div class="list_box">
				<div class="list_box_item" v-for='(dataItem,index) in dataList'  :id="index"  :class='{selectDiv:(index+1)==nowData}'  :key='index'  @click="selectData(index)">
					<div class="left_box">
						<div class="left_box_1">
							<span class="span_1">{{dataItem.sku}}</span>
							<span class="span_2">{{dataItem.qty}}</span>
							<span class="span_3">{{dataItem.loc}}</span>
							<span class="span_4">{{dataItem.skuclass}}</span>
		                    <span class="span_5">{{dataItem.barcode}}</span>
						</div>
						<div class="product_name">{{dataItem.descr}}</div>
					</div>
					<div class="right_box"><i class="iconfont icon-xiangyou1"></i></div>
					
				</div>
			</div>
			
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"InventoryList",
		data(){
			return{
				nowData:0,//具体为第几条数据
				dataList:[],//数据列表
				SortCondition1:false,
				SortCondition2:false,
				SortCondition3:false,
				SortCondition4:false,
				SortCondition5:false,
			}
		},
		created(){

			this.nowData = this.$route.query.nowData;

			var dataList = this.$store.state.QueryListData;

			this.dataList = dataList;
		},
		methods:{
			// 返回上一页 传参
			sendParams:function(){

				var that = this;

				this.$router.push({

					name:"InventoryDetails",
					query:{
						nowData:that.nowData
					}
				})
			},
			//选中具体数据
			selectData:function(index){

				this.nowData = index+1;

				this.$router.push({

					name:"InventoryDetails",
					query:{
						nowData:index+1
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.InventoryList{
		&>.container{
			width: 100%;
			height: 100%;
			font-size: 25/50rem;
			.product_name{
				float: left;
				line-height: 60/50rem;
				margin-left: 30/50rem;
				color: green;
			}
			.title{
				display: flex;
				position: absolute;
				width: 95%;
				height: 60/50rem;
				text-align: center;
				line-height: 60/50rem;
			}
			.list_box{
				position:absolute;
				overflow: scroll;
				top: 60/50rem;
				width: 100%;
			    height: 900/50rem;
				height: calc(100vh - 3rem);	
			}
			.list_box_item{
				width:100%;
				padding:20/50rem 0 10/50rem 0;
				border-bottom: 1/50rem solid #999;
				display: flex;
				justify-content: space-between;
			}
			.left_box{ 
				width: 95%;
			}
			.left_box_1{
				display: flex;
				text-align: center;
			}
			.span_1{
				width: 23%;
			}
			.span_2{
				width: 14%;
			}
			.span_3{
				width: 17%;
			}
			.span_4{
				width: 18%;
				display: block;
				padding:0 10/50rem;
			}
			.span_5{
				width: 32%;
			}
			.right_box{
				width: 5%;
				margin: auto 0;
			}
			.selectDiv{
				background-color:rgba(0,0,0,0.08);
			}
		}
	}
	@media screen and (max-width: 320px){

		.InventoryList{
			&>.container{
				&>div:nth-child(1){
					&>span:nth-child(2){
						span{
							width: 90/50rem;
						}
					}
					.iconfont{
						font-size: 50/50rem;
					}
				}
			}
		}
	}
	@media screen and (max-width: 374px) {	
		.InventoryList{
			&>.container{
				&>div{
					border-bottom: 3/50rem solid #999;
				}
			}
		}
	}
</style>


