<template>
	<div  class="RFPickLoc">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>拣货库位</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div class="import">
				<div class="top">
				<span>客户</span>
				<input  placeholder="请输入内容"   @focus="g_focus('StorerKey')"  @keyup.enter="skipSku"   ref="StorerKey"  v-model="StorerKey" ></input>
				<i v-if="StorerKey" @click="clearInput('StorerKey')" class="iconfont icon-cuowu"></i>			
				<i class="iconfont icon-sousuo"  @click="toSelectStorerKey" ></i>
			</div>
			</div>

			<div class="import">
				<div class="top">
				<span class="product_bar_code">代码/条码</span>
				<input  placeholder="请输入商品代码/条码"   @focus="g_focus('Sku')"   @keyup.enter="query"  ref="Sku"  v-model="Sku"></input>
				<i v-if="Sku" @click="clearInput('Sku','Descr')" class="iconfont icon-cuowu"></i>
			</div>
			</div>

			<div class="goods_name">
				<span>商品名称</span>
				<textarea  v-model="Descr" ref="Descr"  readonly></textarea>
			</div>

			<div class="import readonlyColor">
				<span>基本单位</span>
				<input  v-model="BasicUnit" readonly></input>
			</div>

			<div class="import">
				<div class="top">
				<span>库位</span>
				<input  placeholder="请输入内容"   @focus="g_focus('Loc')"  @keyup.enter="skipALtSku"   ref="Loc"  v-model="Loc" ></input>
				<i v-if="Loc" @click="clearInput('Loc')" class="iconfont icon-cuowu"></i>
			</div>
			</div>
			
			<div class="import">
				<span>补货触发点</span>
				<input  placeholder="请输入内容"   @focus="g_focus('ALtSku')"  ref="ALtSku"  v-model="ALtSku" ></input>
				<i v-if="ALtSku" @click="clearInput('ALtSku')" class="iconfont icon-cuowu"></i>
			</div>

			<span class="g_singleBtn" @click="altSkuBound">确认</span>
		</div>
		<div v-if="tipsHide" @touchmove.prevent class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p  @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
		<div v-if="ifTips" class="msg_tips">
	      <div class="tips_content">
	        <i class="iconfont icon-chenggong1"></i>
	        <p>{{tips_Msg}}</p>
	      </div>
	    </div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"RFPickLoc",
		data(){
			return{
				StorerKey:'',
				tipsHide:false,//提示框是否出现
				msg:"",//提示信息
				inputFocus:"",
				SkuBound:"",
				Sku:"",
				Descr:"",
				ALtSku:"",
				StoreKey_select:"",
				Loc:"",
				BasicUnit:"",//基本单位
		        ifTips:false,
		        tips_Msg:"",
			}
		},
		mounted() {			
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
			if(!this.StorerKey){
			this.$refs['StorerKey'].focus();
			}
			if(this.StorerKey){
				this.$refs['Sku'].focus();
			}
			if(this.StorerKey && this.Sku){
				this.$refs['ALtSku'].focus();
			}
		},
		created(){

			var that = this;
			
			// 客户代码
			if(this.$route.query.StorerKey){

				that.StorerKey = that.$route.query.StorerKey;
			}

			// 在输入框填写  其他页面传来的商品代码
			if(this.$route.query.Sku){
				that.Sku = that.$route.query.Sku;				
			}
			
			// 在输入框填写  其他页面传来的商品名称
			if(this.$route.query.Descr){
				that.Descr = that.$route.query.Descr;				
			}
		},
		methods:{
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				this[inputName] = qrcode;

				if(inputName == "StorerKey") {

					this.getFocus('Sku');

				}else if(inputName == "Loc") {

					this.skipALtSku();

				}else if(inputName == "Sku") {

					this.query();

				}
			},
			//跳到商品代码/条码
			skipSku(){

				this.$refs['Sku'].focus();
			},
			//跳到库位
			skipLoc(){

				this.$refs['Loc'].focus();
			},
			skipAltSkuType(){
				this.$refs['AltSkuType'].focus();
			},
			skipALtSku(){
				this.$refs['ALtSku'].focus();
			},
			//根据商品代码查询商品详情 跳到补货触发点
			query(){
				
				var that = this;

				if(this.Sku =='') {
	 				that.msg = "商品代码不能为空";
					that.tipsShow("Sku");
					that.inputFocus = "Sku";
					return;
	 			}

				var currentUrl = this.HOST + "RFReplenishmentTaskQuery.do";

				var postData = {
					StorerKey : this.StorerKey,
					Sku : this.Sku
				}

				this.httpRequest(currentUrl,postData,function(res){
					if(res.data.Msg !='当前客户下,该商品不存在,请核实后再输入！'){
						that.Descr = res.data.data[0].descr;
						that.BasicUnit = res.data.data[0].susr2;
						that.Loc = res.data.data[0].loc;
						that.ALtSku = res.data.data[0].qtymin;
						that.$refs['Loc'].focus();
					}else{
						that.tipsShow("Sku");

						that.msg = res.data.Msg;

						that.inputFocus = "Sku";
					}
				},"Sku")
								
			},
			//返回上一页 首页
			toIndex:function(){
				this.$router.push({
					name:"Index",
				})
			},
			// 跳转到 选择客户页面
			toSelectStorerKey:function(){

				var that = this;

				var currentUrl = this.HOST + "RFAltSkuBoundWarehouseCodeStoerKey.do";

				var postData = {
					StorerKey : this.StorerKey,
				}

				this.httpRequest(currentUrl,postData,function(res){

					if(res.data.data.length){

						that.$store.state.RFPick_SelectOrderData = res.data.data;

						that.$router.push({
							name:"StoreKey_select",
							query:{
								StorerKey : that.StorerKey,
								WhoPush : "RFPickLoc",
							}
						})
						
					}else{

						that.tipsShow("StorerKey");

						that.msg = "未查询到数据";

						that.inputFocus = "StorerKey";
					}
				},"StorerKey")
			},
			
			//确认修改库存和补货触发点
			altSkuBound:function(){

				var that = this;
				if(this.StorerKey =='') {
 				that.msg = "客户代码不能为空";
				that.tipsShow("StorerKey");
				that.inputFocus = "StorerKey";
 			   }else
				if(this.Loc =='') {
 				that.msg = "库位不能为空";
				that.tipsShow("Loc");
				that.inputFocus = "Loc";
 			   }else
				if(this.Sku =='') {
 				that.msg = "商品代码不能为空";
				that.tipsShow("Sku");
				that.inputFocus = "Sku";
 			   }else
				if(this.ALtSku =='') {
 				that.msg = "补货触发点不能为空";
				that.tipsShow("ALtSku");
				that.inputFocus = "ALtSku";
 			  }else{
				var currentUrl = this.HOST + "RFReplenishmentTaskUpdate.do";
				var postData = {
					Sku:this.Sku,
					StorerKey : this.StorerKey,
					Loc:this.Loc,
					QtyMin:this.ALtSku,
				}

				this.httpRequest(currentUrl,postData,function(res){

					that.ifTips = true;
		            that.tips_Msg = "操作成功";
		            setTimeout(function(){
		              that.ifTips = false;
		            },2000)

					that.StorerKey="";
					that.Sku="";
					that.ALtSku="";
					that.Descr="";
					that.Loc="";
					that.BasicUnit="";
				},)
				
				}
			}
		}
	}
</script>
<style scoped lang="less">
.RFPickLoc{
	&>.container{
		input::placeholder{
			font-size: 25/50rem;
		}
		input{
			font-size: 25/50rem;
		}
		&>div{
			width:100%;
			height: 85/50rem;
			padding-top: 25/50rem; 
			box-sizing: border-box;
			position: relative;
			overflow: hidden;
			span{
				width: 130/50rem;
				height: 55/50rem;
				line-height: 55/50rem;
				text-align: right;
				float: left;
				margin-right: 10/50rem;
			}
			input{
				margin-left: 10/50rem;
				width:480/50rem;	
				height:55/50rem;
				padding:0 10/50rem;
			}
			select{
				margin-left: 10/50rem;		
				width:506/50rem;	
				height:55/50rem;
				padding:0 10/50rem;	
			}						
			i.icon-cuowu{
				position:absolute;
				right:110/50rem;
				font-size: 30/50rem;
				top: 40/50rem;
			}
			i.icon-sousuo{
				position:absolute;
				right:30/50rem;
				font-size: 40/50rem;
				top: 30/50rem;
			}
			textarea{
				width:480/50rem;
				padding: 0 10/50rem;	
				height:100/50rem;
				float: left;
				background-color: #f0f0f0;
				margin-left: 10/50rem;
				outline: none;
			}
		}
		&>div.goods_name{
			width:100%;
			height: 130/50rem;
			box-sizing: border-box;
			position: relative;
			overflow: hidden;
		}
		.readonlyColor{
			input{
				background-color: #f0f0f0;
			}
		}
		.product_bar_code{
			font-size: 26/50rem;
		}
		.g_singleBtn{
			margin-top: 80/50rem;
		}
	}
}
@media screen and (max-width: 329px) and (min-width: 291px){
	.RFPickLoc{
		&>.container{
			font-size: 38/50rem;
			input::placeholder{
				font-size: 35/50rem;
			}
			input{
				font-size: 35/50rem;
			}
			&>div{
				height: 100/50rem;
				padding-top: 20/50rem;
				span{
					width: 160/50rem;
					height: 65/50rem;
					line-height: 65/50rem;
					padding-right: 15/50rem;
					padding-left: 10/50rem;
				}
				
				input{
					width:445/50rem;	
					height:65/50rem;
					line-height: 65/50rem;
					margin: 0;
				}
				select{
					margin-left: 0/50rem;
					width:470/50rem;
					height:65/50rem;
					line-height: 65/50rem;
				}
				textarea{
					width: 445/50rem;
					margin-left: 0;
				}
				i.icon-cuowu{
					position:absolute;
					right:110/50rem;
					font-size: 42/50rem;
					top: 35/50rem;
				}
				i.icon-sousuo{
					right:25/50rem;
					font-size:50/50rem;
					top: 28/50rem;
				}
			}
			&>div.goods_name{				
				height: 140/50rem;
			}
			.g_singleBtn{
				margin-top: 30/50rem;
			}
		}
	}
}
@media screen and (max-width: 290px){
	.RFPickLoc{
		&>.container{
			font-size: 42/50rem;
			input::placeholder{
				font-size: 40/50rem;
			}
			input{
				font-size: 40/50rem;
			}
			&>div{
				height: 110/50rem;
				line-height: 110/50rem;	
				padding-top: 25/50rem;
				span{
					width: 185/50rem;
					height: 75/50rem;
					line-height: 75/50rem;
					float: left;
				}		
				input{					
					width:445/50rem;	
					height:70/50rem;
					margin-left: 0;
					float: left;
			
				}
				select{			
					width:470/50rem;	
					height:75/50rem;
					margin: 0;
					float:left;
					padding:0;
				}
				textarea{
					width: 465/50rem;
					margin-left: 0;
					height: 110/50rem;
					padding: 0;
				}
				i.icon-cuowu{
					position:absolute;
					right:110/50rem;
					font-size: 48/50rem;
					top: 10/50rem;
				}
				i.icon-sousuo{
					right:20/50rem;
					font-size:56/50rem;
					top: 14/50rem;
				}
			}
			&>div.goods_name{
				height: 150/50rem;
			}
		}
	}
}
</style>


